(function() {
  'use strict';

  angular
    .module('michaMicha')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function() {
  'use strict';

  angular
      .module('michaMicha')
      .service('fbRef', fbRef)
      .service('fbAuth', fbAuth)
  ;

  /** @ngInject */
  function fbRef(fbURL) {
    return new Firebase(fbURL);
  }

  /** @ngInject */
  function fbAuth($q, $firebase, $firebaseAuth, fbRef) {
    var auth;
    return function () {
      if (auth) return $q.when(auth);
      var authObj = $firebaseAuth(fbRef);
      if (authObj.$getAuth()) {
        return $q.when(auth = authObj.$getAuth());
      }
      var deferred = $q.defer();
      authObj.$authAnonymously().then(function(authData) {
        auth = authData;
        deferred.resolve(authData);
      });
      return deferred.promise;
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('michaMicha')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('michaMicha')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, webDevTec, toastr) {
    var vm = this;

    vm.awesomeThings = [];
    vm.classAnimation = '';
    vm.creationDate = 1448757295728;
    vm.showToastr = showToastr;

    activate();

    function activate() {
      getWebDevTec();
      $timeout(function() {
        vm.classAnimation = 'rubberBand';
      }, 4000);
    }

    function showToastr() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
      vm.classAnimation = '';
    }

    function getWebDevTec() {
      vm.awesomeThings = webDevTec.getTec();

      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    }
  }
})();

(function() {
    'use strict';

    angular
        .module('michaMicha')
        .controller('CreditsController', CreditsController)
        .controller('CreditController', CreditController);

    /** @ngInject */
    function CreditsController($log, $state, $firebaseArray, fbRef) {
        var vm = this;
        var query = fbRef.child('credits').orderByChild("timestamp").limitToLast(25);
        vm.credits = $firebaseArray(query);
        $log.log('credits: ', vm.credits);

        vm.status = function (credit) {
            var target = credit.status == 'pending' ? 'credits.pending' : 'credits.status';
            $state.go(target, {id: credit.$id});
        };
    }

    /** @ngInject */
    function CreditController($log, $state, $stateParams, $firebaseArray, $firebaseObject, fbRef) {
        $log.log('ID: ', $stateParams.id);
        var vm = this;
        vm.today = new Date();

        var creditRef = fbRef.child('credits').child($stateParams.id);
        vm.credit = new $firebaseObject(creditRef);
        vm.share_link = $state.href('credits.lend', {id: vm.credit.$id}, {absolute: true});

        $log.log('credit: ', vm.credit);
        vm.credit.$loaded()
            .then(function(data) {
                vm.share_body = "¿Me apoyas con un crédito para " + vm.credit.title + "?\n"+
                    vm.share_link;

                var profilesRef = fbRef.child('users').child(vm.credit.user);
                vm.requester = new $firebaseObject(profilesRef);
                $log.log('requester: ', vm.requester);

                $log.log('vm.credit.$id: ', vm.credit.$id);
                var query = fbRef.child('transactions').orderByChild('credit').equalTo(vm.credit.$id);
                vm.transactions = $firebaseArray(query);
                $log.log('transactions: ', vm.transactions);

                vm.transactions.$loaded()
                    .then(function(){
                        vm.lent = {total: 0, count: 0};
                        vm.paid = {total: 0, count: 0};
                        angular.forEach(vm.transactions, function(transaction) {
                            if(transaction.user != vm.credit.user) {
                                vm.lent.count++;
                                vm.lent.total += transaction.amount;
                            } else {
                                vm.paid.count++;
                                vm.paid.total += transaction.amount;
                                vm.paid.last = {
                                    total: transaction.amount,
                                    date: transaction.timestamp
                                };
                            }
                        });
                        vm.lent.left = vm.credit.amount - vm.lent;
                        vm.lent.progress = vm.lent / vm.credit.amount;
                        vm.paid.left = vm.credit.amount - vm.lent;
                        vm.paid.progress = vm.lent / vm.credit.amount;
                    });
            });
    }
})();

